// Animations 

@keyframes scrollAnimation {
  0% { 
    opacity: 0;
  }

  15% { 
    top: 0px;
    opacity: 1;
  }

  50% {
    opacity: 1;
  }

  100% { 
    top: 5px; 
    opacity: 0;
  }
}

